body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #ab0c04;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #920a03;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #ab0c04;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #920a03;
  cursor: pointer;
}

.boton-verde {
  background-color: #878790;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #7a7a84;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #878790;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

/** UI **/
.inicio {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5)), url(../../../build/img/fondo1.png);
  filter: grayscale(100%);
}

.fondo {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(../../../build/img/fondo1.png);
}
@media (min-width: 768px) {
  .fondo {
    background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), url(../../../build/img/ultimo3.png);
    background-position: center center;
    background-size: cover;
  }
}

.header {
  background-color: #ab0c04;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 40rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 40rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 45rem;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.barra img {
  width: 50%;
  margin: auto;
}
@media (min-width: 768px) {
  .barra img {
    width: 90%;
    height: 9rem;
    margin: 0 3rem 5rem 0;
  }
}

.logo-inicio img {
  width: 100%;
  height: 15rem;
  margin: 3rem 2rem 5rem 0;
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.5rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: rgb(175, 10, 10);
}

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}
.icono img {
  width: 40%;
  margin: auto;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin: auto;
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  padding: 2rem 1rem 2rem 1rem;
  margin: 1rem 1rem 1rem 1rem;
}
.anuncio .contenido-anuncio {
  padding: 1rem;
}
.anuncio h3 {
  margin: 0;
}
.anuncio p {
  margin: 0;
  text-align: justify;
}
.anuncio .img1, .anuncio .img2 {
  height: 30rem;
}

.precio {
  color: #878790;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  list-style: none;
  padding: 0;
  max-width: 22rem;
}
.iconos-caracteristicas li {
  display: block;
}
.iconos-caracteristicas li img {
  margin: auto;
}
.iconos-caracteristicas li p {
  font-weight: 900;
  margin: auto;
}

@media (min-width: 480px) {
  .productos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 3rem 3rem 3rem 3rem;
    gap: 3rem;
  }
}
@media (min-width: 768px) {
  .productos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 3rem 3rem 3rem 3rem;
    gap: 4rem;
  }
}
.productos h2 {
  font-size: 2rem;
  font-weight: bold;
}
.productos img {
  height: 50%;
  width: 75%;
  margin: auto;
}

.category {
  border: 1px solid #c8c8c8;
  width: auto;
  height: 35rem;
}

.slider {
  display: grid;
  grid-template-columns: repeat(1fr, 7fr, 1fr);
  width: 95%;
  margin: auto;
  max-height: 32rem;
  overflow: hidden;
  position: relative;
}

.slider_section {
  display: flex;
  max-width: 50rem;
  max-height: 55rem;
}

.section_img {
  display: flex;
  width: 100%;
}

.img1 {
  width: 100%;
}

.slider_btn {
  position: absolute;
  display: flex;
}

.btn-right {
  right: 10px;
}

.btn-left {
  left: 10px;
}

.search {
  text-align: left;
  margin-left: 0;
  font-size: 1.5rem;
  width: 100%;
}
@media (min-width: 480px) {
  .search {
    width: 50%;
  }
}
.search h2 {
  text-align: left;
  font-size: 2rem;
  margin-bottom: 5px;
}
.search .form-search {
  padding: 1rem;
  margin-bottom: 3rem;
}
.search .form-search label {
  margin: 2rem;
}
.search .form-search select {
  width: 50%;
}
.search .form-search input {
  margin: 2rem;
  padding: 2rem;
  background-color: #e1e1e1;
  border: none;
}

.what {
  text-align: center;
  display: flex;
  font-size: 2.5rem;
  align-items: center;
}

.btn-what {
  width: 50%;
}

.img-art {
  max-width: 90%;
  max-height: 70%;
}

.imagen-contacto {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../../../build/img/images (3).jpg");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #ab0c04;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #878790;
  margin-top: 1rem;
}

.testimonial {
  background-color: #878790;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .form-contacto {
  margin: 0;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.form-icons {
  padding: 2rem;
  margin-left: 0;
  margin-top: 13rem;
}
@media (min-width: 480px) {
  .form-icons {
    margin-top: 10rem;
  }
}
@media (min-width: 768px) {
  .form-icons {
    margin-top: 0rem;
  }
}
.form-icons img {
  max-height: 50rem;
  background-color: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
}
.form-icons ul {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-left: 0;
}
.form-icons ul li {
  list-style: none;
}
.form-icons ul li a {
  width: 80px;
  height: 80px;
  background-color: rgb(225, 224, 224);
  text-align: center;
  line-height: 80px;
  font-size: 35px;
  margin: 10px 7px 0px 0px;
  display: block;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid rgb(225, 224, 224);
  z-index: 1;
}
@media (min-width: 480px) {
  .form-icons ul li a {
    margin: 10px 10px -80px 10px;
  }
}
.form-icons ul li a .icon {
  position: relative;
  color: #262626;
  transition: 0.5s;
  z-index: 3;
}
.form-icons ul li a:hover .icon {
  color: #fff;
  transform: rotateY(360deg);
}
.form-icons ul li a:before {
  content: "";
  position: absolute;
  margin-top: 4%;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f00;
  transition: 0.5s;
  z-index: 2;
}
.form-icons ul li a:hover:before {
  top: 0;
}
.form-icons ul li:nth-child(1) a:before {
  background: #3b5999;
}
.form-icons ul li:nth-child(2) a:before {
  background: #e223a6;
}
.form-icons ul li:nth-child(3) a:before {
  background: #00b518;
}
.form-icons ul li:nth-child(4) a:before {
  background: #dd4b39;
}

.tablas-admin h2 {
  font-weight: bold;
}

.tabla {
  margin: 2rem 2rem 2rem 2rem;
}
.tabla thead {
  background-color: rgb(170, 12, 12);
  color: #FFFFFF;
  padding: 2rem;
  font-size: 2rem;
  width: 100%;
}
.tabla thead .encabezado0 {
  width: 20%;
}
.tabla thead .encabezado {
  width: 10%;
}
.tabla thead .encabezado3 {
  display: none;
